@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff"), url("../fonts/opensans.woff2");
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff"), url("../fonts/opensansbold.woff2");
  font-weight: bold;
}
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  color: #444444;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 260px;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 0;
  margin-left: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .container {
    width: 670px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1050px;
  }
}
.page-header {
  display: flex;
  min-width: 320px;
  border-bottom: 1px solid #ececec;
}
.section-header {
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  margin: 0;
}
.section-header--contacts-header {
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
}
@media (min-width: 768px) {
  .section-header {
    font-size: 43px;
    font-weight: 700;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .section-header {
    font-size: 53px;
    font-weight: 700;
    line-height: 57px;
  }
}
.site-button {
  border: 2px solid #444444;
  background-color: white;
  color: #444444;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  box-sizing: border-box;
  height: 52px;
  display: block;
}
.site-button:hover {
  background-color: #444444;
  color: #ffffff;
}
.site-button:active {
  color: 070707;
}
.main-nav {
  position: relative;
  background-color: white;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  z-index: 2;
}
.main-nav.container {
  width: 320px;
}
.main-nav__logo {
  text-align: center;
  padding-top: 22px;
  padding-bottom: 13px;
}
.main-nav__logo-image {
  width: 86px;
  height: 35px;
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:active {
  opacity: 0.3;
}
.main-nav__delivery {
  display: none;
}
.main-nav__sale {
  display: none;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 80px;
  height: 78px;
  background-color: white;
  cursor: pointer;
  border: none;
  outline: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 32px;
  left: 30px;
  width: 20px;
  height: 2px;
  background-color: #221f1f;
  box-shadow: 0 6px 0 0 #221f1f, 0 11px 0 0 #221f1f;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 15px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}
.main-nav--opened .main-nav__toggle {
  bottom: 0;
  right: 0;
  order: 1;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 40px;
  right: 30px;
  width: 30px;
  height: 3px;
  background-color: #221f1f;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: left;
  }
  .main-nav--nojs .user-list__item {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav.container {
    width: 670px;
  }
  .main-nav {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
    margin-bottom: -44px;
  }
  .main-nav__logo {
    padding: 0;
    margin: auto;
    margin-left: 0;
    margin-top: 22px;
  }
  .main-nav__logo-image {
    width: 113px;
    height: 46px;
  }
  .main-nav__list {
    order: 2;
    display: flex;
    justify-content: space-between;
    width: 305px;
  }
  .main-nav__user-options {
    display: flex;
  }
  .site-list__search span {
    display: none;
  }
  .main-nav__delivery {
    margin: 0;
    padding-top: 15px;
    display: block;
    line-height: 24px;
    order: 3;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1150px) {
  .main-nav.container {
    width: 1050px;
  }
  .main-nav__logo {
    margin: 0;
    order: 1;
    flex-direction: row;
  }
  .main-nav__logo-image {
    width: 149px;
    height: 72px;
    padding-top: 10px;
  }
  .main-nav__list {
    width: 400px;
    order: 0;
    justify-content: flex-start;
  }
  .main-nav__user-options {
    order: 3;
    margin-left: 54px;
  }
  .main-nav__sale {
    display: flex;
