  display: block;
  position: relative;
  padding-left: 62px;
}
.fieldset__radio label:hover::before {
  border: 2px solid #63d1bb;
}
.fieldset__radio label:active::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
.fieldset__radio input {
  display: none;
}
.fieldset__radio input + label::before {
  content: "";
  position: absolute;
  width: 39px;
  height: 39px;
  top: -9px;
  left: 0;
  border: 2px solid #444444;
  border-radius: 50%;
  box-sizing: border-box;
}
.fieldset__radio input:checked + label::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 2px;
  left: 11px;
  background-color: #63d1bb;
  border-radius: 50%;
}
.fieldset__checkbox label {
  display: block;
  position: relative;
  padding-left: 62px;
}
.fieldset__checkbox label:hover::before {
  border: 2px solid #63d1bb;
}
.fieldset__checkbox label:active::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
.fieldset__checkbox input {
  display: none;
}
.fieldset__checkbox input + label::before {
  content: "";
  position: absolute;
  width: 39px;
  height: 39px;
  top: -11px;
  left: 0;
  border: 2px solid #444444;
  box-sizing: border-box;
}
.fieldset__checkbox input:checked + label::after {
  content: "";
  position: absolute;
  width: 22px;
  height: 25px;
  top: -5px;
  left: 10px;
  background: url("../img/icon-tick.svg") no-repeat 50% 50%;
}
